.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 5rem;
	background-color: rgb(151, 119, 119);
	color: white;
	display: flex;
	justify-content:center;
	align-items: center;
	padding: 0;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	z-index: 10;
	border-radius: 5px;
}

.header p{
	position: fixed;
	top: 2rem;
	right: 2rem;

}